.App-link {
  color: #61dafb;
}


.header { 
  background: -webkit-linear-gradient(235deg,rgba(255, 196, 0, 0.9),rgb(255, 196, 0, 0) 70%), -webkit-linear-gradient(320deg, rgba(255, 0, 132, 0.8), rgb(255,0,0,0) 70%), -webkit-linear-gradient(80deg, rgba(183, 0, 255, 0.9), rgb(255,0,0,1) 100%); 
  background: linear-gradient(215deg,rgba(255, 196, 0, 0.9),rgb(255, 196, 0, 0) 70%), linear-gradient(130deg, rgba(255, 0, 132, 0.8), rgb(255,0,0,0) 70%), linear-gradient(10deg, rgba(183, 0, 255, 0.9), rgb(255,0,0,1) 100%);
  border-radius: 0% 60%;
  height: 100px;
  display: -webkit-flex;
  display: flex;
  text-align: center;
  color: white;
  font-size: calc(10px + 2vmin);
}

.header__logo{
  max-width: 100px;
  margin-left: 10px;
}

.header__store-title { 
  font-family: "Mr Dafoe";
  font-size: 50px;
  font-weight: 100;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  margin-top: 15px;
  margin-left: 65px;
}

.header__cart { 
  -webkit-flex: 0 1 150px; 
          flex: 0 1 150px;
  text-align: right
}


.header__cart-link{ 
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background: none;
  font-family: "Muli";
  font-size: 20px;
  font-weight: bold;
  color: rgba(255, 0, 132);
  padding-right: 15px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}

.header__cart-link:hover { 
 color: orange;
}

.header__cart-link:focus { 
 outline: none;
}









.collection-viewer { 
  width: 80%;
  margin: auto;
}

.collection-viewer__select{ 
  border: none;
  text-align: left;
  position: relative;
}

.collection-viewer__select-dropdown{
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  background: none;
  border: 1px solid orange;
  font-family: "Muli";
  font-size: 20px;
  margin: 10px 20px;
  padding: 3px;
  -webkit-transition: border 0.5s;
  transition: border 0.5s;
}

.collection-viewer__select-dropdown:hover{
  border:  1px solid rgba(255, 0, 132);
}

.collection-viewer__select-dropdown:hover{
  border:  1px solid rgba(255, 0, 132);
}

.collection-viewer__select-dropdown:focus{
  outline: none;
}

.collection-viewer__select::after{
  width: 10px;
  height: 10px;
  content: " ";
  border: 3px solid orange;
  border-radius: 3px;
  border-top: none;
  border-left:none;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  position: absolute;
  top: 17px;
  left: 290px;
  z-index: 1;
  -webkit-transition: border 0.5s;
  transition: border 0.5s;
}

.collection-viewer__select::after:focus{
  border:  3px solid rgba(255, 0, 132);
} 

.product{
  -webkit-flex: 1 0 30vh;
          flex: 1 0 30vh;
  margin: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: space-between;
          justify-content: space-between
}
.product__image { 
  width: 100%;
}
.product__button { 
  height: 25px;
  width: 100%;
  color: white;
  background: #EB1B99;
  border-radius: 5px;
  font-size: 14px;
  font-weight: bold;
  font-family: "Muli";
  margin-top: 10px;
}


.variant-select  { 
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  background: none;
  border: 1px solid orange;
  font-family: "Muli";
  font-size: 14px;
  margin: 5px 0;
  padding: 3px;
  -webkit-transition: border 0.5s;
  transition: border 0.5s;
  width: 100%
}
.collection__product-container{ 
  display: -webkit-flex; 
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
.cart { 
  z-index: 100;
  position: fixed;
  background: white;
  width: 800px;
  min-height: 400px;
  top: 200px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  box-shadow: 3px 3px 3px 3px #eeeeee;
}

.cart__header { 
  display: -webkit-flex; 
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.cart__header__title { 
  -webkit-flex: 1 0 auto; 
          flex: 1 0 auto;
}

.cart__header__close { 
  -webkit-flex: 0 1 auto; 
          flex: 0 1 auto;
  margin: 3px;
}
